class ValidatorList {
	constructor(msg = '  '){
		this.errorMessage = msg;
	}
	toString() {
		return this.errorMessage;
	}
}

export class Required extends ValidatorList {
	constructor () {
		super('   ');
	}
	test(value) {
		return value.length > 0;
	}
}

export class Repeated extends ValidatorList {
    constructor () {
        super('  ');
    }
    test(value) {
        return value === document.getElementById('reg_password').value;
    }
}

export class MinLength extends ValidatorList {
	constructor () {
		super('   7 ');
	}
	test(value) {
		return value.length >= 7;
	}
}

export class MaxLength extends ValidatorList {
	constructor () {
		super('   100 ');
	}
	test(value) {
		return value.length <= 100;
	}
}

export class Email extends ValidatorList {
	constructor () {
		super(' - ');
	}
	test(value) {
		let reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
		return reg.test(value);
	}
}

export class Selected extends ValidatorList {
	constructor () {
		super(' ');
	}

	test(index) {
		return index >= 0 || index === 'new' || index === 'used';
	}
}
